.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100vh;
  color: #aaffaa;
  background: #222;
  font-family: monospace;
}

.App h2 {
  white-space: pre-wrap;
  margin: 20px;
}

.App h2:after {
  content: "\2588";
  -webkit-animation: 1s fade infinite;
          animation: 1s fade infinite;
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

