      flex-direction: row;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 30px;
  color: white;
}

.App .moar {
  font-size: 20px;
  margin-left: 20px;
}

.App .controls__button {
  border: 2px solid white;
  border-radius: 50%;
  padding: 10px;
  overflow: visible;
  fill: white;
  width: 50px;
  height: 50px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App .controls__button:hover {
  opacity: 0.8;
  cursor: pointer;
}



